import xflSelect from '../../../components/xfl-select/xfl-select.vue'
var  graceChecker = require("../../../common/graceChecker.js");
export default {
	data() {
		return {
			title: 'checkbox 复选框',
			PageCur: 'Home',
			CustomBar: this.CustomBar,
			modalName: null,
			modalNameS: null,
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
			imgList: [],
			switchA: false,
			switchB: true,
			switchC: false,
			switchD: false,
			radio: 'A',
			checkbox: [{
				value: 'A',
				checked: false
			}, {
				value: 'B',
				checked: true
			}, {
				value: 'C',
				checked: false
			}],
			width:120,
			height:45,
			list1: [ //国家地区 {value: '香蕉', disabled: true},
			    '中国',
			    '中国香港',
			    '中国澳门',
			    '中国台湾',
			],
			list2: [ 
			    '中国共产党党员',
			    '中国共产党预备党员',
			    '中国共产党员（保留团籍)',
			    '中国共产主义青年团团员',
				'中国国民党革命委员会会员',
				'中国民主同盟会会员',
				'中国民主建国会会员',
				'中国民主促进会会员',
				'中国农工民主党党员',
				'中国致公党党员',
				'九三学社社员',
				'台湾民主自治同盟盟员',
				'无党派民主人士',
				'中国少年先锋队队员',
				'群众'
			],
			list3: [ //
			    '汉族',
			    '藏族',
			    '苗族',
			    '维吾尔族',
				'彝族',
				'壮族',
				'蒙古族',
				'回族',
				'土家族'
			],
			list4: [ //
			    '四川省',
			    '北京市',
			    '天津市',
			    '河北省',
				'重庆市'
			],
			list5: [
			    '自贡市','成都市', '绵阳市', '德阳市','宜宾市','资阳市',
				'泸州市','攀枝花市','广元市','遂宁市','内江市','巴中市',
				'乐山市','南充市','眉山市','广安市','达州市','雅安市','阿坝藏族羌族自治州'
			],
			list6: [ //
			    '博士研究生','硕士研究生','大学本科', 
			    '大学专科','中等专科','职业高中','技工学校',
				'高中','初中','小学','其他'
			],
			list7: [ 
			    '国家公务员','专业技术人员','职员','企业管理人员',
				'工人','农民','学生','教师',
				'现役军人','自由职业者','个体经营者','无业人员','退休（离休）人员','其他'
			],
			list8: [ 
			    '大安街道办事处',
			    '凉高山街道办事处',
			    '龙井街道办事处',
			    '马冲口街道办事处',
			],
			list9: [
			    '半小时',
			    '一小时',
			    '半天',
			    '一天',
				'两天'
			],
		}
	},
	mounted:function(){
		var _self=this;
		setTimeout(function(){
			// _self.init();
		},2000)
	},
	components: { xflSelect },  //注册为子组件
	methods: {
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		 PickerChange(e) {
			this.index = e.detail.value
		},
		SwitchA(e) {
			this.switchA = e.detail.value
		},
		SwitchB(e) {
			this.switchB = e.detail.value
		},
		SwitchC(e) {
			this.switchC = e.detail.value
		},
		SwitchD(e) {
			this.switchD = e.detail.value
		},
		CheckboxChange(e) {
			var items = this.checkbox,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				items[i].checked = false;
				for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
					if (items[i].value == values[j]) {
						items[i].checked = true;
						break
					}
				}
			}
		},
		
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		showModalS(e) {
			this.modalNameS = e.currentTarget.dataset.target
		},
		hideModalS(e) {
			this.modalNameS = null
		},
		ChooseCheckbox(e) {
			let items = this.checkbox;
			let values = e.currentTarget.dataset.value;
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
				if (items[i].value == values) {
					items[i].checked = !items[i].checked;
					break
				}
			}
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		onKeyInput: function(event) {
		    this.inputValue = event.target.value
		  },
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '照片',
				content: '确定要删除该照片吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		checkboxChange: function (e) {
		                var items = this.items,
		                    values = e.detail.value;
		                for (var i = 0, lenI = items.length; i < lenI; ++i) {
		                    const item = items[i]
		                    if(values.includes(item.value)){
		                        this.$set(item,'checked',true)
		                    }else{
		                        this.$set(item,'checked',false)
		        }
		    }
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		    //定义表单规则
		    var rule = [
			    {name:"user", checkType : "int", checkRule:"1,8",  errorMsg:"用户名为必填项"},
				{name:"pass", checkType : "string", checkRule:"1,50",  errorMsg:"密码为必填项"},
				{name:"passcheck", checkType : "string", checkRule:"1,50",  errorMsg:"确认密码为必填项"},
				{name:"mail", checkType : "email", checkRule:"1,50",  errorMsg:"邮箱为必填项"},
				{name:"nickname", checkType : "string", checkRule:"1,4",  errorMsg:"姓名应为1-4个字符"},
				{name:"idtype", checkType : "string", checkRule:"1,50",  errorMsg:"证件类型为必填项"},
				{name:"idnum", checkType : "byid", checkRule:"1,50",  errorMsg:"证件号码为必填项"},
				{name:"num", checkType : "phoneno", checkRule:"1,50",  errorMsg:"手机号码为必填项"},
				{name:"address", checkType : "string", checkRule:"1,50",  errorMsg:"详细地址为必选项"},
				{name:"date", checkType : "dating", checkRule:"1,8",  errorMsg:"出生日期为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"gender", checkType : "in", checkRule:"男,女",  errorMsg:"请选择性别"},
				{name:"agreement", checkType : "notnull",  checkRule:"",  errorMsg:"请同意勾选协议"},
		    ];
		    //进行表单检查
		    var formData = e.detail.value;
		    var checkRes = graceChecker.check(formData, rule);
		    if(checkRes){
		        uni.showToast({title:"验证通过!", icon:"none"});
		    }else{
		        uni.showToast({ title: graceChecker.error, icon: "none" });
		    }
		},
				
	}
}
